.form__group {
    margin-top: 25px;
    width: 50%;
    position: relative;
  }
  
  .form__field {
    min-width: 400px;
    border-radius: 3px;
    color: rgb(49, 49, 49);
    padding: 7px;
    padding-right: 25px;
    background-color: #f2f2f2;
    font-size: 16px;
    border: 1px solid lightgray;
    outline: none;
    height: 20px;
    white-space:nowrap;
    text-overflow:ellipsis;
  }
  
  .cross {
    width: 17px;
    height: 17px;
    object-fit: cover;
    position: absolute;
    top: 10px;
    left: 410px;
    cursor: pointer;
  }
  
  .cross__disabled {
    opacity: .2;
  }
  
  .cross:hover {
    box-shadow: -5px -5px 5px -5px rgba(185, 197, 206, 0.6) inset;
    opacity: .7s;
  }
  
  .form__field:focus {
    box-shadow: -5px 3px 20px 7px rgba(185, 197, 206, 0.38);
    white-space:nowrap;
    text-overflow:ellipsis;
  }
  